import React, { useEffect, useRef, useState, useCallback } from "react";
import { Pagination, Select, message } from "antd";
import { getPpiTransactionList } from "../../api/login/Login";
import Loader from "../../common/loader/Loader";
import { FaEye } from "react-icons/fa";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import Table from "react-bootstrap/Table";
import ReactHTMLTableToExcel from "react-html-table-to-excel";

const PpiTransactionReport = () => {
  const getCurrentDate = () => new Date().toISOString().slice(0, 10);

  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [excelData, setExcelData] = useState([]);
  const [selectedRow, setSelectedRow] = useState(null);

  const [count, setCount] = useState(10);
  const [page, setPage] = useState(0);
  const [totalCount, setTotalCount] = useState(0);

  const [sortConfig, setSortConfig] = useState({ key: "", direction: "" });

  const effectRun = useRef(false);

  const [filters, setFilters] = useState({
    startDate: getCurrentDate(),
    endDate: getCurrentDate(),
    search: "",
    status: "",
    bank: "",
  });

  const handleFilterChange = (e) => {
    setFilters({ ...filters, [e.target.name]: e.target.value });
  };

  const sortData = (dataList, key) => {
    let direction = "asc";
    if (sortConfig.key === key && sortConfig.direction === "asc") direction = "desc";
    setSortConfig({ key, direction });
    return [...dataList].sort((a, b) => {
      if (a[key] < b[key]) return direction === "asc" ? -1 : 1;
      if (a[key] > b[key]) return direction === "asc" ? 1 : -1;
      return 0;
    });
  };

  const fetchTransactions = useCallback(async () => {
    setLoading(true);
    try {
      const res = await getPpiTransactionList();
      let results = res?.data?.results || [];
      if (filters.search) {
        const val = filters.search.toLowerCase();
        results = results.filter(
          (t) =>
            t.txn_id.toLowerCase().includes(val) ||
            t.remitterMobile.includes(val) ||
            t.beneficiaryAccount.includes(val)
        );
      }
      setData(results);
      setTotalCount(results.length);
    } catch (err) {
      message.error("Failed to fetch PPI Transactions");
    }
    setLoading(false);
  }, [filters, count, page]);

  useEffect(() => {
    if (!effectRun.current) {
      fetchTransactions();
      effectRun.current = true;
    }
  }, [fetchTransactions]);

  const handleSearch = () => {
    setPage(0);
    fetchTransactions();
  };

  const handleReset = () => {
    setFilters({
      startDate: getCurrentDate(),
      endDate: getCurrentDate(),
      search: "",
      status: "",
      bank: "",
    });
    setPage(0);
    fetchTransactions();
  };

  const prepareExcelDownload = () => {
    if (!data.length) return message.error("No data found");
    setExcelData(data);
    setTimeout(() => document.getElementById("excelExportPpi").click(), 200);
  };

  const handleSortClick = (key) => {
    const sorted = sortData(data, key);
    setData(sorted);
  };

  const getSortArrow = (key) =>
    sortConfig.key === key ? (sortConfig.direction === "asc" ? " ▲" : " ▼") : "";

  const handlePageSizeChange = (value) => {
    setCount(value);
    setPage(0);
    fetchTransactions();
  };

  const onChangeVal = (val) => {
    setPage(val - 1);
  };

  return (
    <>
      {loading && <Loader />}

      <div className="PageHeading" style={{ color: "var(--bs-primary3)" }}>
        <h1>Bank PPI Transaction Report</h1>
      </div>

      <div className="ContentArea">

        {/* FILTER CARD */}
        <div
          className="card"
          style={{
            backgroundColor: "var(--bs-primary2)",
            border: "1px solid var(--bs-nav)",
            color: "var(--bs-primary3)",
          }}
        >
          <div className="card-body">
            <div className="form-row" style={{ alignItems: "end" }}>

              <div className="form-group col-md-4">
                <label>Start Date</label>
                <input
                  type="date"
                  className="form-control"
                  name="startDate"
                  value={filters.startDate}
                  onChange={handleFilterChange}
                  style={{
                    backgroundColor: "var(--bs-primary2)",
                    color: "var(--bs-primary3)",
                    border: "1px solid var(--bs-nav)",
                  }}
                />
              </div>

              <div className="form-group col-md-4">
                <label>End Date</label>
                <input
                  type="date"
                  className="form-control"
                  name="endDate"
                  value={filters.endDate}
                  onChange={handleFilterChange}
                  style={{
                    backgroundColor: "var(--bs-primary2)",
                    color: "var(--bs-primary3)",
                    border: "1px solid var(--bs-nav)",
                  }}
                />
              </div>

              <div className="form-group col-md-4">
                <label>Txn Id / Mobile / Account</label>
                <input
                  type="text"
                  className="form-control"
                  name="search"
                  placeholder="Search..."
                  onChange={handleFilterChange}
                  value={filters.search}
                  style={{
                    backgroundColor: "var(--bs-primary2)",
                    color: "var(--bs-primary3)",
                    border: "1px solid var(--bs-nav)",
                  }}
                />
              </div>

              <div className="form-group col-md-2 mt-3">
                <button
                  className="btn"
                  onClick={handleSearch}
                  style={{
                    backgroundColor: "#6C757D ",
                    color: "#FFF",
                    border: "1px solid var(--bs-nav)",
                  }}
                >
                  Search
                </button>

                <button
                  className="btn ml-2"
                  onClick={handleReset}
                  style={{
                    backgroundColor: "#F8F9FA",
                    color: "var(--bs-primary2)",
                    border: "1px solid var(--bs-primary3)",
                  }}
                >
                  Reset
                </button>
              </div>

            </div>
          </div>
        </div>

        {/* TABLE CARD */}
        <div
          className="card mt-4"
          style={{
            backgroundColor: "var(--bs-primary2)",
            border: "1px solid var(--bs-nav)",
            overflow: "auto",
          }}
        >
          <div className="card-body">

            {/* EXCEL BUTTON */}
            <div className="d-flex justify-content-end">
              <Button
                onClick={prepareExcelDownload}
                style={{
                  backgroundColor: "#E8DAD6",
                  color: "var(--bs-primary2)",
                  border: "1px solid var(--bs-nav)",
                }}
              >
                Download Excel
              </Button>

              <ReactHTMLTableToExcel
                id="excelExportPpi"
                filename="PPI_Transaction_Report"
                sheet="Sheet1"
                className="d-none"
                table="tablePpi"
              />
            </div>

            {/* TABLE */}
            <table
              className="table table-bordered table-hover mt-3"
              id="tablePpi"
              style={{ borderColor: "var(--bs-nav)", whiteSpace: "nowrap" }}
            >
              <thead
                style={{
                  backgroundColor: "var(--bs-primary1)",
                  color: "var(--bs-primary2)",
                }}
              >
                <tr>
                  <th>S No</th>
                  <th onClick={() => handleSortClick("createdAt")}>
                    Txn Date {getSortArrow("createdAt")}
                  </th>
                  <th onClick={() => handleSortClick("txn_id")}>
                    Txn ID {getSortArrow("txn_id")}
                  </th>
                  <th>UTR</th>
                  <th>Order ID</th>
                  <th>Mobile No.</th>
                  <th>Account</th>
                  <th>IFSC</th>
                  <th>Debit</th>
                  <th>Amount</th>
                  <th>View</th>
                </tr>
              </thead>

              <tbody style={{ color: "var(--bs-primary3)" }}>
                {data.length > 0 ? (
                  data.map((item, i) => (
                    <tr key={item._id}>
                      <td>{i + 1}</td>
                      <td>{new Date(item.createdAt).toLocaleString()}</td>
                      <td>{item.txn_id}</td>
                      <td>{item.txnReferenceId}</td>
                      <td>{item.orderid}</td>
                      <td>{item.remitterMobile}</td>
                      <td>{item.beneficiaryAccount}</td>
                      <td>{item.beneficiaryIfsc}</td>
                      <td>{item.pool?.mode}</td>
                      <td>₹{item.pool?.amount}</td>
                      <td>
                        <FaEye
                          onClick={() => setSelectedRow(item)}
                          style={{ cursor: "pointer", color: "var(--bs-primary2)" }}
                        />
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan="14" className="text-center">
                      No Transaction Found
                    </td>
                  </tr>
                )}
              </tbody>
            </table>

            {/* PAGINATION */}
            <div className="dataTables_info_page">
              <div className="d-flex align-items-center">
                <div className="dataTables_length me-3">
                  <label className="me-2">Show</label>
                  <Select
                    value={count}
                    onChange={handlePageSizeChange}
                    style={{ width: 120 }}
                    options={[
                      { value: 10, label: "10 / page" },
                      { value: 20, label: "20 / page" },
                      { value: 50, label: "50 / page" },
                      { value: 100, label: "100 / page" },
                    ]}
                  />
                </div>

                <div className="dataTables_info">
                  Total {totalCount} entries
                </div>
              </div>

              <div className="dataTables_paginate paging_simple_numbers">
                <Pagination
                  current={page + 1}
                  onChange={onChangeVal}
                  total={totalCount}
                  pageSize={count}
                  showSizeChanger={false}
                />
              </div>
            </div>

          </div>
        </div>
      </div>

      {/* MODAL */}
      {selectedRow && (
        <Modal
          show
          centered
          onHide={() => setSelectedRow(null)}
        >
          <Modal.Header
            closeButton
            style={{
              backgroundColor: "var(--bs-primary1)",
              color: "var(--bs-primary2)",
            }}
          >
            <Modal.Title>Transaction #{selectedRow.txn_id}</Modal.Title>
          </Modal.Header>

          <Modal.Body
            style={{
              backgroundColor: "var(--bs-primary1)",
              color: "var(--bs-primary3)",
            }}
          >
            <Table borderless>
              <tbody>
                <tr>
                  <td>Txn ID</td>
                  <td>{selectedRow.txn_id}</td>
                </tr>
                <tr>
                  <td>Order ID</td>
                  <td>{selectedRow.orderid}</td>
                </tr>
                <tr>
                  <td>UTR</td>
                  <td>{selectedRow.txnReferenceId}</td>
                </tr>
                <tr>
                  <td>Mobile</td>
                  <td>{selectedRow.remitterMobile}</td>
                </tr>
                <tr>
                  <td>Account</td>
                  <td>{selectedRow.beneficiaryAccount}</td>
                </tr>
                <tr>
                  <td>IFSC</td>
                  <td>{selectedRow.beneficiaryIfsc}</td>
                </tr>
                <tr>
                  <td>Date</td>
                  <td>{new Date(selectedRow.createdAt).toLocaleString()}</td>
                </tr>
                <tr>
                  <td>Debit</td>
                  <td>{selectedRow?.pool?.mode}</td>
                </tr>
                <tr>
                  <td>Amount</td>
                  <td>₹{selectedRow?.pool?.amount}</td>
                </tr>
              </tbody>
            </Table>
          </Modal.Body>

          <Modal.Footer
            style={{
              backgroundColor: "var(--bs-primary2)",
            }}
          >
            <Button
              onClick={() => setSelectedRow(null)}
              style={{
                backgroundColor: "var(--bs-primary1)",
                color: "var(--bs-primary2)",
                border: "1px solid var(--bs-nav)",
              }}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      )}
    </>
  );
};

export default PpiTransactionReport;
